.Label{
    margin-bottom: 53px;
    min-height: 64px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    color: var(--light);
    background: linear-gradient(90deg, rgba(133,75,189,0.8828125) 0%, rgba(133,75,189,0.8127844887955182) 53%, rgba(188,87,214,0.014465161064425747) 100%);
    font-family: var(--font-highlighted);
    font-weight: 600;
}

.Label_btn{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    height: 64px;
    background: transparent;
    border: none;
    cursor: pointer;
}

.Label_burger{
    width: 40px;
    height: 40px;
    background: red;
}

@media(min-width: 768px){
    .Label{
        border-radius: 144px;
    }
}